import request from '@/utils/request'

export const getUserInfo = (data: any) =>
  request({
    url: '/account/userInfo',
    method: 'post',
    data
  })

export const login = (data: any) =>
  request({
    url: '/account/login',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    method: 'post',
    params: data
  })

export const logout = () =>
  request({
    url: '/account/logout',
    method: 'post'
  })
